:root{font-size:16px}body{background-color:#282d34;color:#fff;padding:0;margin:0;box-sizing:border-box;font-family:"Open Sans";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-y:scroll}.page-container{display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:1.25rem;min-height:80vh}.page-container>*{width:calc(min(80vw, 45rem))}.info-box{background-color:#1f2428;padding:1.5rem;border-radius:2rem;transition:-webkit-transform ease 0.5s;transition:transform ease 0.5s;transition:transform ease 0.5s, -webkit-transform ease 0.5s}.info-box h1:first-child,.info-box h2:first-child,.info-box h3:first-child,.info-box h4:first-child,.info-box h5:first-child,.info-box h6:first-child{margin-top:0}.info-box:hover{-webkit-transform:scale(1.03);transform:scale(1.03);-webkit-transform-origin:50%, 50%;transform-origin:50%, 50%}a{color:#fff;font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;transition:-webkit-text-decoration ease 0.2s;transition:text-decoration ease 0.2s;transition:text-decoration ease 0.2s, -webkit-text-decoration ease 0.2s;-webkit-text-decoration:underline solid;text-decoration:underline solid;-webkit-text-decoration-color:rgba(255,255,255,0.7);text-decoration-color:rgba(255,255,255,0.7);text-decoration-thickness:2px}a:hover{-webkit-text-decoration-color:white;text-decoration-color:white}.link-button{display:flex;justify-content:space-around;align-items:center;margin:1rem;padding:0.5rem;border-radius:20px;border:1px solid #1f2428;background-color:#1f2428;transition:0.2s;text-decoration:none}.link-button>*{margin:0.25rem}.link-button:hover{background-color:#282d34;border:5px solid #1f2428}.center{text-align:center}.image-caption{color:#afafaf;font-size:0.8em;width:100%}h1,h2,h3{font-family:Montserrat;margin-left:0;margin-top:0.5rem;margin-bottom:0.5rem}hr{width:auto;height:0;border:0.2rem solid #1f2428;border-radius:15px;margin-bottom:1.5rem}.info-box>hr{border-color:#282d34}ul{padding-left:1.5rem}ul>li{padding-bottom:0.25rem}img{display:block;margin-left:auto;margin-right:auto}svg{height:1em}.fade-in{opacity:0;-webkit-transform:translateY(1rem);transform:translateY(1rem);-webkit-animation:fadeIn 0.7s ease-in-out 0.2s forwards;animation:fadeIn 0.7s ease-in-out 0.2s forwards}.fade-in--no-move{opacity:0;-webkit-animation:fadeInNoMove 0.4s ease-in-out 0.1s forwards;animation:fadeInNoMove 0.4s ease-in-out 0.1s forwards}@-webkit-keyframes fadeIn{from{opacity:0;-webkit-transform:translateY(1rem);transform:translateY(1rem)}to{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes fadeIn{from{opacity:0;-webkit-transform:translateY(1rem);transform:translateY(1rem)}to{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@-webkit-keyframes fadeInNoMove{from{opacity:0}to{opacity:1}}@keyframes fadeInNoMove{from{opacity:0}to{opacity:1}}

.navbar{width:100%;height:calc(min(15vw, 4rem));background-color:#1f2428;display:flex;justify-content:space-between;margin-bottom:1rem}.nav-elements{display:flex;height:100%;flex-direction:row;justify-content:space-between;align-items:center}a.nav-link{display:block;height:100%;min-width:4rem;padding-left:1rem;padding-right:1rem;text-decoration:none;font-size:1.5rem;transition:ease 0.05s}a.nav-link:hover{background-color:#282d34}a.nav-link:hover>.nav-item-contents{-webkit-transform:translate(0, -0.2rem);transform:translate(0, -0.2rem)}.nav-item-contents{display:flex;justify-content:center;align-items:center;height:100%;margin:0;transition:ease-out 0.4s}.home-button a{font-style:italic}.nav-link .tooltip{font-style:normal;font-size:.75em;font-family:"Open Sans";display:inline-block;position:absolute;-webkit-transform:translateY(2.5rem);transform:translateY(2.5rem);opacity:0;transition:all ease .5s}.nav-link:hover .tooltip{-webkit-transform:translateY(3rem);transform:translateY(3rem);opacity:1}

#root{position:relative;min-height:100vh}#root>#footer{position:absolute;right:0;bottom:0;left:0;height:auto;text-align:center}.page-container{padding-bottom:6rem}.social-links{display:inline-flex;justify-content:center;align-items:center;padding:0.5rem;margin-left:auto;margin-right:auto}.social-links .link-button{box-sizing:border-box;font-size:1.25rem;width:10rem}.social-links .link-button svg{flex-shrink:0}

.introduction{align-items:center;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;margin-top:2rem;margin-bottom:4rem;text-align:right}.introduction>.col{box-sizing:border-box;flex-basis:50%;flex-grow:1}.introduction img{text-indent:100%;max-width:85%;width:auto;min-width:14rem}.introduction h1{font-size:2.25rem;background:linear-gradient(#22d3ee, #0891b2);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent}.introduction p{text-align:center}@media screen and (max-width: 768px){.introduction{flex-direction:column;text-align:center}.introduction p{text-align:center}.introduction .col:first-child{margin-bottom:2rem}}img.profile{height:auto;overflow:hidden;border-radius:50%;background-color:#1f2428;transition:ease-out 0.4s;box-shadow:0px 0px 20px 5px #1f2428}img.profile:hover{-webkit-transform:scale(1.025);transform:scale(1.025);-webkit-transform-origin:50%, 50%;transform-origin:50%, 50%}.skills-list{align-items:center;margin-top:20px;display:flex;flex-wrap:wrap;justify-content:space-evenly;width:100%}.skills-list .skill-item{margin-bottom:1rem;width:24%;display:inherit;flex-direction:column;align-items:center;justify-content:center}.skills-list .skill-item>svg{font-size:4rem}@media screen and (max-width: 768px){.skills-list .skill-item{width:33%}.skills-list .skill-item>svg{font-size:3rem}}

.card-list{display:grid;justify-items:center;align-items:center;justify-content:center;width:calc(min(850px, 90vw));grid-gap:0rem;grid-template-columns:repeat(auto-fit, minmax(400px, 1fr))}.project-card{background-color:#1f2428;width:calc(min(400px, 90%));height:30rem;margin:1rem;border-radius:2rem;overflow:hidden;transition:all ease 0.5s}.project-card>img{object-fit:cover;object-position:50% 50%;width:100%;height:10rem;border-radius:1rem;box-shadow:0px 0px 30px 0px #282d34;margin-bottom:1rem;transition:ease 0.8s;z-index:-1;background-color:#121314}.project-card:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.project-card:hover img{-webkit-transform:scale(1.05);transform:scale(1.05)}.project-card h1,.project-card h2,.project-card h3{text-align:center}.project-card p{margin:1rem;text-indent:2ch;text-align:justify}.project-card a{margin:1rem}.project-card hr{border-color:#282d34;background-color:#282d34}pre{margin-top:0.5rem}

